"use client"
import { useState, useEffect, useRef } from "react"
import { supabase } from "@/utils/supabase/alSupabase"
import { useRouter } from "next/navigation";
import { useAuth } from "../things/hooks/useAuth";
import { useMessages } from "../things/hooks/useMessages";
import { sighOut } from "../things/utils/auth";

export default function Page() {
    const router = useRouter();
    const [user, setUser] = useState<any>(null);
    const [profile, setProfile] = useState<any>(null);

    const [inbox, setInbox] = useState<any[]>([]);
    const [sent, setSent] = useState<any[]>([]);

    const [isOpen, setIsOpen] = useState(false);

    const idRef = useRef<HTMLInputElement>(null);
    const textRef = useRef<HTMLInputElement>(null);
    const [isAnon, setIsAnon] = useState(false);

    const [tab, setTab] = useState<"inbox" | "sent">("inbox");
    
    useEffect(() => {
        const getUser = async () => {
            const {data: authData, error: authError} = await supabase.auth.getUser()

            if (authError || !authData.user) {
                setUser(null)
                setProfile(null)
                return
            }

            setUser(authData.user)

            const {data: profileData, error: profileError} = await supabase
                .from("users")
                .select("public_id, username")
                .eq("id_user", authData.user.id)
                .single()

            if(!profileError) {
                setProfile(profileData)
            }
        }
        getUser()

        const {data: listener} = supabase.auth.onAuthStateChange((event, session) => {
            setUser(session?.user || null)
        })

        return () => {
            listener.subscription.unsubscribe()
        }
    }, []);

    const getMessages = async () => {
            const {data: inboxRes, error: inboxError} = await supabase
                .from("messages_safe")
                .select(`
                    id,
                    body,
                    created_at,
                    is_anon,
                    from_user,
                    from_public_id,
                    from_username
                    `)
                .eq("to_user", user.id)
                .order("created_at", {ascending: true})
            if (inboxError) {
                console.log(inboxError)
                return
            }

            const {data: sentRes, error: sentError} = await supabase
                .from("messages")
                .select(`
                    id,
                    body,
                    created_at,
                    is_anon,
                    to_user: to_user (
                        public_id,
                        username
                    )
                    `)
                .eq("from_user", user.id)
                .order("created_at", {ascending: true})
            if (sentError) {
                console.log(sentError)
                return
            }

            setInbox(inboxRes || [])
            setSent(sentRes || [])
        }
    useEffect(() => {
        if (!user) return
        getMessages()
    }, [user])


    const sendMessage = async () => {
        const publicId = idRef.current?.value;
        const text = textRef.current?.value;
        if (!publicId || !text) {
            alert("все поля сладкий заполни")
        }

        const {data: receiver, error: userError} = await supabase
            .from("users")
            .select("id_user")
            .eq("public_id", publicId)
            .single()

        if(userError || !receiver) {
            console.log(userError.message)
            return
        }

        const {error: messageError} = await supabase
            .from("messages")
            .insert({
                from_user: user.id,
                to_user: receiver.id_user,
                body: text,
                is_anon: isAnon
            });
        
        if(messageError){
            console.log(messageError.message)
            return
        }

        idRef.current!.value = ""
        textRef.current!.value = ""
        setIsOpen(false)
        getMessages()
    }

    return (
        <div className="relative bg-black min-h-screen flex flex-col">
            {user ? (
                <header className="bg-[#8f184f] text-white p-1 flex justify-between items-center shadow-md">
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-full bg-red-500 flex items-center justify-center text-lg font-bold">
                            {profile?.public_id.charAt(1).toUpperCase()}
                        </div>
                        <div className="flex flex-col">
                            <span className="font-semibold">{profile?.public_id}</span>
                        </div>
                    </div>
                    <button
                        className="bg-white/10 hover:bg-white/20 text-white px-4 py-1 rounded-md transition"
                        onClick={async () => {
                            await supabase.auth.signOut();
                            setUser(null);
                            router.push('/anonlove/auth')
                        }}
                    >
                        Выйти
                    </button>
                </header>
            ) : (
                <header className="bg-gray-900 text-white p-4 shadow-md">
                    <span>Вы не залогинены</span>
                </header>
            )}

            {/* сообщения */}
            <main className="flex-1 p-6 text-white flex">
                <div className="bg-[#12080b] border-2 border-[#8f184f] rounded-lg w-1/2 min-h-full">
                    <div className="flex flex-1">
                        <button 
                            onClick={() => setTab("inbox")} 
                            className="bg-white/10 p-2 rounded-md w-1/2 text-center flex items-center gap-1"
                        >
                            <span>Сообщения</span>
                            <span 
                                className={`w-3 h-3 rounded-full border transition
                                    ${tab === "inbox"
                                        ? "bg-white border-white"
                                        : "border-white/50"
                                    }    
                                `}
                            />
                        </button>

                        <button 
                            onClick={() => setTab("sent")} 
                            className="bg-white/10 p-2 rounded-md w-1/2 text-center flex items-center gap-1"
                        >
                            <span>Отправленные</span>
                            <span 
                                className={`w-3 h-3 rounded-full border transition
                                    ${tab === "sent"
                                        ? "bg-white border-white"
                                        : "border-white/50"
                                    }    
                                `}
                            />
                        </button>
                    </div>


                    <div className="flex flex-col items-start gap-2 p-2">
                        {tab === "inbox" 
                            ?
                            inbox.map((message) => (
                              <div 
                                key={message.id} 
                                className="flex items-center gap-2"
                                >
                                <div className="w-10 h-10 rounded-full bg-red-500 flex items-center justify-center text-lg font-bold">
                                    {message.is_anon 
                                    ? "A"
                                    : message.from_public_id?.charAt(1).toUpperCase()}
                                </div>

                                <div className="flex flex-col">
                                    <span className="font-semibold">
                                        {message.is_anon 
                                        ? "Anon"
                                        : message.from_public_id}
                                    </span>
                                    <span className="">Тебе тут что то пришло, малышка!</span>
                                </div>
                            </div>  
                            ))


                            :
                            sent.map((message) => (
                              <div 
                                key={message.id} 
                                className="flex items-center gap-2"
                                >
                                <div className="w-10 h-10 rounded-full bg-red-500 flex items-center justify-center text-lg font-bold">
                                    {message.is_anon ? "A" : profile.public_id.charAt(1).toUpperCase()}
                                </div>
                                <div className="flex flex-col">
                                    <span className="font-semibold">{
                                    message.is_anon 
                                        ? `Anon > ${message.to_user.public_id}`
                                        : `${profile.public_id} > ${message.to_user.public_id}`
                                    
                                    }</span>
                                    <span className="">{message.body}</span>
                                </div>
                            </div>  
                            ))
                        }
                        
                    </div>
                    <div className="w-1/2 mx-auto bg-red-700 h-[1px]"></div>
                </div>

                <div className="flex w-1/2 justify-center items-center">
                    <AnimatedButton
                    openModal={() => setIsOpen(true)}
                    >
                        Давай отправим что нибудь!
                    </AnimatedButton>
                </div>
            </main>

            {/* modalka */}
            {isOpen && (
                <div 
                    className="absolute w-[400px] h-[300px] bg-black z-50 
                                rounded-lg flex border-2 border-[#8f184f]
                                top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"> 
                    <form 
                        className="relative flex flex-col justify-center items-center gap-2 mx-auto w-full"
                        onSubmit={(e) => {e.preventDefault()}}
                        >
                        <input
                            ref={idRef}
                            type="text" 
                            placeholder="id няшки" 
                            className="p-2 border-2 rounded-lg "
                            />
                        <input 
                            ref={textRef}
                            type="text" 
                            placeholder="Текст" 
                            className="p-2 border-2 rounded-lg "
                            />
                        <div className="flex justify-center items-center">
                            <input
                                type="checkbox" id="anon" name="anon"
                                checked={isAnon}
                                onChange={(e) => {setIsAnon(e.target.checked)}}
                            />
                            <label htmlFor="anon">{`анонимно (увидит только id)`}</label>
                        </div>
                        

                        <button type="button" onClick={sendMessage}>Отправить</button>

                        <button onClick={() => {setIsOpen(false)}} className="absolute top-1 right-2">X</button>
                    </form>
                </div>
            )}
        </div>
    )

}


function AnimatedButton({ 
    children,
    openModal 
}: {
    children: React.ReactNode,
    openModal: () => void

}) {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const buttonRef = useRef<HTMLButtonElement>(null);
    const animationRef = useRef<number>(0);

    useEffect(() => {
        const canvas = canvasRef.current
        const button = buttonRef.current
        if(!canvas || !button) return;

        const ctx = canvas.getContext("2d");
        if(!ctx) return;

        const updateSize = () => {
            const rect = button.getBoundingClientRect();
            canvas.width = rect.width;
            canvas.height = rect.height;
        }
        updateSize()

        const w = canvas.width
        const h = canvas.height
        const perimeter = 2 * (w + h)
        let distance = 0
        const speed = 300

        let lastTime = performance.now()

        function animate(currentTime: number) {
            if (!ctx || !canvas) return

            const deltaTime = (currentTime - lastTime) / 1000
            lastTime = currentTime

            ctx.clearRect(0,0, canvas.width, canvas.height)

            distance = (distance + speed * deltaTime) % perimeter

            let x,y;

            if (distance < w) {
                x = distance
                y = 0
            } else if (distance < w + h) {
                x = w
                y = distance - w
            } else if (distance < 2 * w + h){
                x = w - (distance - w - h) 
                y = h
            } else {
                x = 0
                y = h - (distance - 2 * w - h)
            }

            // Рисуем точку с многослойным свечением
            // Внешнее свечение
            ctx.shadowBlur = 0;
            ctx.globalAlpha = 0.2;
            ctx.fillStyle = 'white';
            ctx.beginPath();
            ctx.arc(x, y, 7, 0, Math.PI * 2);
            ctx.fill();

            // Среднее свечение
            ctx.globalAlpha = 0.4;
            ctx.beginPath();
            ctx.arc(x, y, 6, 0, Math.PI * 2);
            ctx.fill();

            // Внутреннее свечение
            ctx.shadowBlur = 8;
            ctx.shadowColor = 'white';
            ctx.globalAlpha = 0.7;
            ctx.beginPath();
            ctx.arc(x, y, 4, 0, Math.PI * 2);
            ctx.fill();

            // Яркое ядро
            ctx.shadowBlur = 4;
            ctx.globalAlpha = 1;
            ctx.beginPath();
            ctx.arc(x, y, 2, 0, Math.PI * 2);
            ctx.fill();

            animationRef.current = requestAnimationFrame(animate)

        }

        animationRef.current = requestAnimationFrame(animate)

        window.addEventListener('resize', updateSize)

        return () => {
            if (animationRef.current){
                cancelAnimationFrame(animationRef.current)
            }
            window.removeEventListener('resize', updateSize)
        }
    }, [])

    return (
        <div className="relative inline-block">
            <canvas
                ref={canvasRef}
                className="absolute top-0 left-0 pointer-events-none"
            />

            <button
                ref={buttonRef}
                onClick={openModal}
                className="relative px-8 py-4 bg-white/10 rounded-md hover:bg-white/20 transition-colors"
            >
                {children}
            </button>
        </div>
    )
}
